import { SortDescriptor } from '../sort-descriptor';
import { isPresent, isBlank } from '../utils';
import { getter } from '../accessor';

/**
 * @hidden
 */
type Comparer = (a: any, b: any) => number;

/**
 * @hidden
 */
const compare = (a, b) => {
    if (isBlank(a)) {
        return a === b ? 0 : -1;
    }

    if (isBlank(b)) {
        return 1;
    }

    if (a.localeCompare) {
       return a.localeCompare(b);
    }
    return a > b ? 1 : (a < b ? -1 : 0);
};

/**
 * @hidden
 */
const compareDesc = (a, b) => compare(b, a);

/**
 * @hidden
 */
const initial: Comparer = (a, b) => 0;

/**
 * @hidden
 */
const descriptorToCompare = (descriptor: SortDescriptor) => {
    const prop = getter(descriptor.field);
    return (...args) => (descriptor.dir === 'asc' ? compare : compareDesc)
        .apply(this, args.map(x => prop(x)));
};

/**
 * Orders the specified array by the provided sort descriptors.
 */
export const orderBy = <T>(data: T[], descriptors: SortDescriptor[]): T[] => {
    data = data.slice(0);

    const comparer = descriptors
        .filter(x => isPresent(x.dir))
        .reduce(
            (acc: Comparer, curr: SortDescriptor): Comparer => (
                (a, b) => acc(a, b) || descriptorToCompare(curr)(a, b)
            ),
            initial
        );

    data.sort(comparer);

    return data;
};



